import React from "react";
import { Route, Redirect } from "react-router-dom";
import {LoaderTailSpin} from './login-ui/Loader'
import { connect } from "react-redux";

const ProtectedRoute = ({
    component: Component,
    isAuthenticated,
    isVerifying,
    ...rest
  }) => (
    <Route
      {...rest}
      render={props =>
        isVerifying ? (
          <LoaderTailSpin></LoaderTailSpin>
        ) : isAuthenticated ? (
          <Component {...props} />
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: props.location }
            }}
          />
        )
      }
    />
  );
  export default ProtectedRoute;