import {
    LOGIN_REQUEST,
    LOGIN_SUCCESS,
    LOGIN_FAILURE,
    LOGOUT_REQUEST,
    LOGOUT_SUCCESS,
    LOGOUT_FAILURE,
    VERIFY_REQUEST,
    VERIFY_SUCCESS
} from "../actions/"

export default (state = {
    isLoggingIn: false,
    isLoggingOut: false,
    isVerifying: false,
    loginError: false,
    logoutError: false,
    isAuthenticated: false,
    user: {}
}, action) => {
    switch (action.type) {
        case LOGIN_REQUEST:
            return {
                ...state,
                isVerifying:true,
                isLoggingIn: true,
                loginError: false
            };
        case LOGIN_SUCCESS:
            return {
                ...state,
                isLoggingIn: false,
                isAuthenticated: true,
                isVerifying:false,
                user: action.user
            };
        case LOGIN_FAILURE:
            return {
                ...state,
                isLoggingIn: false,
                isAuthenticated: false,
                loginError: true,
                isVerifying:false
            };
        case VERIFY_REQUEST:
            return {
                ...state,
                isVerifying: true,
                verifyingError: false,
            };
        case VERIFY_SUCCESS:
            return {
                ...state,
                isVerifying: false
            };
        case LOGOUT_REQUEST:
            return {
                ...state,
                isLoggingOut:true,
            };
        case LOGOUT_SUCCESS:
            return {
                ...state,
                isAuthenticated:false
            };
        case LOGOUT_FAILURE:
            return {
                ...state,
                logoutUser: true,
            };
        default:
            return state;
    }
}