import React, { useState } from 'react';
import { connect } from "react-redux";
import { withStyles, Button, TextField, Typography, Container } from "@material-ui/core";
import IconButton from "@material-ui/core/IconButton";
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import {useHistory} from 'react-router-dom'
const StyleAvatar =
{
    marginLeft: "auto",
    marginRight: "auto",
    marginTop: 20,
    backgroundColor: "#f50057",
    display: "block",
    color: "white",
    outline: "none"
}
const styles = () => ({

    "@global": {
        body: {
        }
    },
    welcomeTitle:
    {
        fontSize: "6vw",
        textAlign: "center",
        color: "black".user,
        fontFamily: "cursive",
        margin: 20
    },
    question:
    {
        fontSize: "3vw",
        textAlign: "center",
        margin: 20
    },
    textbox:
    {
        margin: 10
    }
});

function Welcome(props) {
    let history = useHistory();
    const { classes } = props;
    const [displayname,setName] = useState("");

    function handleNext(){
        var user = props.user;
        console.log();
        user.updateProfile({
            displayName: displayname
        }).then(function () {
            console.log('his');
            history.push("/dashboard");
        }).catch(function (error) {
            // An error happened.
        });
    }
        return (
            <Container>
                <div style={{ textAlign: "center" }}>
                    <Typography component="p" className={classes.welcomeTitle}>Welcome!</Typography>
                    <Typography component="p" className={classes.question}>Please Enter your Name</Typography>
                    <TextField variant="outlined"
                        margin="normal"
                        id="displayname"
                        label="Display Name"
                        name="displayname"
                        onChange={e => setName(e.target.value)}
                        className={classes.textbox}
                        autoFocus></TextField>
                    <IconButton onClick={handleNext} style={StyleAvatar}>
                        <ArrowForwardIosIcon></ArrowForwardIosIcon>
                    </IconButton>
                </div>
            </Container>
        )
    }

function mapStateToProps(state) {
    return {
        user: state.auth.user
    }
}
export default withStyles(styles)(connect(mapStateToProps)(Welcome));