import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect, Link } from "react-router-dom";
import { loginUser, loginUserWithGmail } from "../actions";
import { withStyles } from "@material-ui/core/styles";
import googleLogo from "../images/btn_google_signin_dark_normal_web.png"
import Button from "@material-ui/core/Button"; 
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Container from "@material-ui/core/Container";
import {FamilySocialTitle,LoaderTailSpin} from './login-ui/';


const styles = () => ({
  "@global": {
    body: {
      backgroundColor: "#fff"
    }
  },
  paper: {
    marginTop: 30,
    display: "flex",
    padding: 20,
    flexDirection: "column",
    alignItems: "center",
    boxShadow: "0 30px 40px 0 rgba(16, 36, 94, 0.2)"
  },
  form: {
    marginTop: 1,
    textAlign:"center"
  },
  errorText: {
    color: "#f50057",
    marginBottom: 5,
    textAlign: "center"
  }
  
});

class Login extends Component {
  state = { email: "", password: "" };

  handleEmailChange = ({ target }) => {
    this.setState({ email: target.value });
  };

  handlePasswordChange = ({ target }) => {
    this.setState({ password: target.value });
  };

  handleEmailSignin = () => {
    const { dispatch } = this.props;
    const { email, password } = this.state;

    dispatch(loginUser(email, password));
  };

  handleGoogleSignin = () => {
    const { dispatch } = this.props;
    dispatch(loginUserWithGmail());
  }
  handleSignup = () => {
    console.log('signup');
    return <Redirect to='/signup' />
  }

  render() {
    const { classes, loginError, isAuthenticated,isVerifying } = this.props;
    if(isVerifying)
      return <LoaderTailSpin></LoaderTailSpin>
    else if (isAuthenticated) {
      return <Redirect to="/" />;
    }
    else {
      return (
        <Container component="main" maxWidth="xs">
          <FamilySocialTitle></FamilySocialTitle>
          <Paper className={classes.paper}>
            <form className={classes.form}>

            <TextField
              variant="outlined"
              margin="normal"
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              onChange={this.handleEmailChange}
              autoFocus
            />
            <TextField
              variant="outlined"
              margin="normal"
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              onChange={this.handlePasswordChange}
            />
            {loginError && (
              <Typography component="p" className={classes.errorText}>
                Invalid Email or Password.
              </Typography>
            )}
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              style={{backgroundColor:"#f50057",color:"white",borderRadius:'15px',marginTop:30}}
              onClick={this.handleEmailSignin}
            >
              Sign In
            </Button>
            </form>
            <br></br>
            <Button
              type="button"
              className={classes.submit}
              onClick={this.handleGoogleSignin}
            ><img src={googleLogo} /></Button><br></br>
            <span>
              Not a member? <Link style={{ textDecorationLine: 'underline' }} to={{
                pathname: "/signup"
              }}
              >Sign Up Now</Link></span>
          </Paper>
        </Container>
      );
    }
  }
}

function mapStateToProps(state) {
  return {
    isLoggingIn: state.auth.isLoggingIn,
    loginError: state.auth.loginError,
    isAuthenticated: state.auth.isAuthenticated,
    isVerifying:state.auth.isVerifying
  };
}

export default withStyles(styles)(connect(mapStateToProps)(Login));