import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import { Button, Snackbar } from '@material-ui/core';
import PersonPinIcon from '@material-ui/icons/PersonPin';
import myFirebase from '../../firebase/firebase';
import { firebase } from '@firebase/app';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import Sentimeter from './sentimeter';
const useStyles = makeStyles((theme) => ({
  root: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
    margin: 'auto',
    border: "1px solid white",
    borderTop:'4px solid red',
    "&:focus-within": {
    borderTop:'4px solid red',
      border: "1px solid red",
    },
    marginBottom: 15
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
    marginTop: 17
  },
  iconButton: {
    margin: 'auto 20px auto 20px'
  },
  post:
  {
    margin: '0px 15px 15px auto',
    outline: 'none !important',
  }
}));

export function CreateUtter() {
  const classes = useStyles();
  const [value, setValue] = React.useState();
  const [open, setOpen] = React.useState(false);
  const [sentimeterValue,setSentimeterValue] = React.useState(50);
  const [sentimeter,setSentimeter] = React.useState(50);

  function handleSentimeterChange(newValue)
  {
    setSentimeterValue(newValue.sentimeterValue);
    setSentimeter(newValue.sentimeter);
  }
  function postUtter() {
  var db = myFirebase.firestore();
  var user = myFirebase.auth().currentUser;
 
console.log(user);
  db.collection("posts").add({
      content: value,
      createdAt: firebase.firestore.FieldValue.serverTimestamp(),
      createdBy:user.uid,
      sentimeterValue:sentimeterValue,
      sentiment:sentimeter,
      userAvatar:user.displayName?user.displayName.split("")[0]:user.email.split("")[0]
    })
      .then((docRef) => {
        setValue("");
        setOpen(true);
      })
      .catch((error) => {
        console.error("Error adding document: ", error);
      });
  }
  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };
  return (
    <Paper elevation={2} component="form" className={classes.root}>
      <div className="container-fluid">
        <div className="row">
          <PersonPinIcon className={classes.iconButton} color="error" />
          <InputBase
            className={classes.input}
            placeholder="Say It !"
            inputProps={{ 'aria-label': 'search google maps' }}
            multiline
            rows="2"
            value={value}
            onChange={event => {                                 //adding the onChange event
              setValue(event.target.value)
            }}
          />
        </div>
          <Sentimeter handleChange={handleSentimeterChange} className="row"></Sentimeter>
        <div className="row">
      <Button  
            variant="contained"
            color="secondary"
            className={classes.post}
            size="small"
            onClick={postUtter}
          >
            Utter
      </Button>
        </div>
      </div>
      <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        open={open}
        autoHideDuration={1000}
        onClose={handleClose}
        message="Utter Successful"
        action={
          <React.Fragment>
            <Button color="secondary" size="small" onClick={handleClose}>
              UNDO
            </Button>
            <IconButton size="small" aria-label="close" color="inherit" onClick={handleClose}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </React.Fragment>
        }
      />
    </Paper>
  );
}
