import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import MoreVertIcon from '@material-ui/icons/MoreVert';

const useStyles = makeStyles((theme) => ({
    root: {
        marginTop: 10,
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: ({ color }) =>  color,
    },
}));
function timeDiff(curr) {
    var ms_Min = 60 * 1000; // milliseconds in Minute 
    var ms_Hour = ms_Min * 60; // milliseconds in Hour 
    var ms_Day = ms_Hour * 24; // milliseconds in day 
    var diff = -new Date(curr * 1000) + Date.now(); //difference between dates. 
    if (diff < ms_Min) {
        return Math.round(diff / 1000) + ' seconds ago';
    } else if (diff < ms_Hour) {
        return Math.round(diff / ms_Min) + ' minutes ago';
    } else if (diff < ms_Day) {
        return Math.round(diff / ms_Hour) + ' hours ago';
    } else {
        return curr;
    }
}
function getSentiment(value)
  {
    if(value<'10')
      return ("Hate It!");
    else if(value>='10'&&value<='20')
    return("Angry!");
    else if(value>='21'&&value<='31')
    return("Sad :(");
    else if(value>='32'&&value<='40')
    return("Curious!");
    else if(value>='41'&&value<='55')
    return("Just Saying!");
    else if(value>='56'&&value<='69')
    return("Feeling Good!");
    else if(value>='70'&&value<='80')
    return("Being Happy!");
    else if(value>='90'&&value<='100')
    return('Love!');
  }
export function PostCard(inputProps){
    var color;
    if(inputProps.data.sentimeterValue<30)
        color='red';
    else if(inputProps.data.sentimeterValue>=30&&inputProps.data.sentimeterValue<=70)
        color='green';
    else if(inputProps.data.sentimeterValue>70)
        color='blue';
    const classes = useStyles({color});
    return (
        <Card className={classes.root} elevation={0} variant="outlined">
            <CardHeader avatar={
                    <Avatar aria-label="recipe" className={classes.avatar}>{inputProps.data.userAvatar}</Avatar>
                }
                action={
                    <IconButton aria-label="settings">
                        <MoreVertIcon />
                    </IconButton>
                }
                title={getSentiment(inputProps.data.sentimeterValue)}
                subheader={timeDiff(inputProps.data.createdAt.seconds)}
            />
            <CardContent className="pb-1 ml-2">
                <Typography variant="body1" component="p">
                    {inputProps.data.content}
                </Typography>
            </CardContent>
            <CardActions disableSpacing>
                <div className="ml-auto">
                    <IconButton aria-label="add to favorites">
                        <FavoriteIcon />
                    </IconButton>
                    {/* <IconButton aria-label="share">
                        <ShareIcon />
                    </IconButton> */}
                </div>
            </CardActions>
        </Card>
    );
}
