import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect, Link } from "react-router-dom";
import { signupUser } from "../actions";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Container from "@material-ui/core/Container";
import {FamilySocialTitle} from "./login-ui/FamilySocialTitle"

const styles = () => ({
    "@global": {
        body: {
            backgroundColor: "#fff"
        }
    },
    paper: {
        marginTop: 40,
        display: "flex",
        padding: 20,
        flexDirection: "column",
        alignItems: "center",
        boxShadow: "0 30px 40px 0 rgba(16, 36, 94, 0.2)"
    },
    form: {
        marginTop: 1
    },
    errorText: {
        color: "#f50057",
        marginBottom: 5,
        textAlign: "left",
        fontSize:13
    }
});

class SignUp extends Component {
    state = { email: "", password: "", ispasswordmatch: true };

    handleEmailChange = ({ target }) => {
        this.setState({ email: target.value });
    };

    handlePasswordChange = ({ target }) => {
        this.setState({ password: target.value });
    };
    handleConfirmPasswordChange = ({ target }) => {
        if (this.state.password != target.value)
            this.setState({ ispasswordmatch: false });
        else
            this.setState({ ispasswordmatch: true });

    };

    handleSignup = () => {
        const { dispatch } = this.props;
        dispatch(signupUser(this.state.email, this.state.password))
    }

    render() {
        const { classes, loginError, isAuthenticated } = this.props;
        if (isAuthenticated) {
            return <Redirect to="/" />;
        }
        else {
            return (
                <Container component="main" maxWidth="xs">
                    <FamilySocialTitle></FamilySocialTitle>
                    <Paper className={classes.paper}>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            id="email"
                            label="Email Address"
                            name="email"
                            onChange={this.handleEmailChange}
                        />
                        <TextField
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            name="password"
                            label="Password"
                            type="password"
                            id="password"
                            onChange={this.handlePasswordChange}
                        />
                        <TextField
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            name="confirmpassword"
                            label="confirm Password"
                            type="password"
                            id="confirmpassword"
                            onChange={this.handleConfirmPasswordChange}
                        />
                        {!this.state.ispasswordmatch && (
                            <Typography component="p" className={classes.errorText}>
                                Please make sure Password and Confirm Password match
                            </Typography>
                        )}
                        {loginError && (
                            <Typography component="p" className={classes.errorText}>
                                Not able to Create your account. Please try again later
                            </Typography>
                        )}
                        <br></br>
                        <Button
                            type="button"
                            fullWidth
                            variant="contained"
                            style={{ backgroundColor: "#f50057", color: "white", borderRadius: '15px' }}
                            className={classes.submit}
                            onClick={this.handleSignup}
                        >Sign Up</Button><br></br>
                        <span>Already a member? <Link to="/login"
                            color="primary"
                            className={classes.submit}
                        >Sign In</Link></span>
                    </Paper>
                </Container>
            );
        }
    }
}

function mapStateToProps(state) {
    return {
        isLoggingIn: state.auth.isLoggingIn,
        loginError: state.auth.loginError,
        isAuthenticated: state.auth.isAuthenticated
    };
}

export default withStyles(styles)(connect(mapStateToProps)(SignUp));