import myFirebase from "../firebase/firebase";
import firebase from "firebase";

export const LOGIN_REQUEST = "LOGIN_REQUEST";
export const LOGIN_SUCCESS = "LOGIN_SUCCESS";
export const LOGIN_FAILURE = "LOGIN_FAILURE";

export const LOGOUT_REQUEST = "LOGOUT_REQUEST";
export const LOGOUT_SUCCESS = "LOGOUT_SUCCESS";
export const LOGOUT_FAILURE = "LOGOUT_FAILURE";

export const VERIFY_REQUEST = "VERIFY_REQUEST";
export const VERIFY_SUCCESS = "VERIFY_SUCCESS";

const requestLogin = () => { return { type: LOGIN_REQUEST }; }

function receiveLogin(user) {
    return { type: LOGIN_SUCCESS, user: user };
}
function loginError() {
    return { type: LOGIN_FAILURE };
}
function requestLogout() {
    return { type: LOGOUT_REQUEST };
}
function receiveLogout() {
    return { type: LOGOUT_SUCCESS };
}
function logoutError() {
    return { type: LOGOUT_FAILURE };
}
function verifyRequest() {
    return { type: VERIFY_REQUEST };
}
function verifySuccess() {
    return { type: VERIFY_SUCCESS };
}

const providers = new firebase.auth.GoogleAuthProvider();
providers.addScope('profile');
providers.addScope('email');

export const loginUser = (email, password) => dispatch => {
    dispatch(requestLogin());
    myFirebase.auth().signInWithEmailAndPassword(email, password)
        .then(result => {
            // var token = result.credential.accessToken;
            // var user = result.user;
            dispatch(receiveLogin(result))
        })
        .catch(error => { dispatch(loginError()) })
}
export const signupUser = (email, password) => dispatch => {
    dispatch(requestLogin());
    myFirebase.auth().createUserWithEmailAndPassword(email, password)
        .then(result => {
            // var token = result.credential.accessToken;
            // var user = result.user;
            dispatch(receiveLogin(result))
        })
        .catch(error => { dispatch(loginError()) })
}

export const loginUserWithGmail = () => dispatch => {
    dispatch(requestLogin());
    var provider = new firebase.auth.GoogleAuthProvider();
    firebase.auth().signInWithRedirect(provider)
        .then(function (result) {
            var user = result.user;
            if (user !== null)
                dispatch(receiveLogin(user))
        }).catch(function (error) {
            dispatch(loginError());
        });
}
export const verifyAuth = () => dispatch => {
    dispatch(verifyRequest());
    myFirebase.auth().onAuthStateChanged(user => {
        if (user !== null)
            dispatch(receiveLogin(user))
        dispatch(verifySuccess());
    })
}

export const logoutUser = () => dispatch => {
    dispatch(requestLogout());
    myFirebase
        .auth()
        .signOut()
        .then(() => {
            dispatch(receiveLogout());
        })
        .catch(error => {
            //Do something with the error if you want!
            dispatch(logoutError());
        });
};