import React, { Component } from "react";
import { connect } from "react-redux";
import { logoutUser } from "../actions";
import { FeedBody } from "./feed-page/feed-body";
import PrimaryAppBar from "./primary-appbar";

class Dashboard extends React.Component {
    handleLogout = () => {
        const { dispatch } = this.props;
        dispatch(logoutUser());
        console.log('logout');
    };

    render() {
        const user = this.props.user;
        return (
            <div className="container-fluid" style={{ backgroundColor: '#bac2d2b3'}}>
                <div className="row">
                   <PrimaryAppBar user ={user}handleLogout={this.handleLogout}></PrimaryAppBar> 
                </div>
                <div className="row">
                    <div className="col-lg-2 col-sm-12"></div>
                    <div className="col-lg-7 col-sm-12 pl-0 pr-0"><FeedBody className="border"></FeedBody></div>
                    <div className="col-lg-3 col-sm-12"></div>

                </div></div>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.auth.user,
        isLoggingOut: state.auth.isLoggingOut,
        logoutError: state.auth.logoutError
    };
}
export default connect(mapStateToProps)(Dashboard);