import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Slider from '@material-ui/core/Slider';

const useStyles = makeStyles({
  root: {
    width: '90%',
    margin:'10px auto'
  },
});

const SentimeterSlider = withStyles({
    root: {
      backgroundImage: "linear-gradient(to right, #ff0000, #ee6100, #d58c00, #b7ad00, #95c800, #6cce4e, #40d17b, #00d1a1, #00b9cb, #009aff, #0071ff, #0400f2)",
      height: 1,
      paddingTop:1,
      paddingBottom:1,
      marginBottom:10
    },
    thumb: {
      height: 24,
      width: 24,
      backgroundColor: '#fff',
      border: '2px solid currentColor',
      marginTop: -11,
      marginLeft: -12,
      '&:focus, &:hover, &$active': {
        boxShadow: 'inherit',
      },
    },
    active: {},
    valueLabel: {
      display:'none',
    },
    track: {
      height: 0,
      borderRadius: 4,
    },
    rail: {
      height: 0,
      borderRadius: 4,
    },
  })(Slider);
  
export default function Sentimeter(props) {
  const classes = useStyles();
  const [senti,setSenti] = React.useState("Just Saying");
  function handleSlider(value)
  {
    if(value<'10')
      setSenti("Hate It!");
    else if(value>='10'&&value<='20')
    setSenti("Angry!");
    else if(value>='21'&&value<='31')
    setSenti("Sad :(");
    else if(value>='32'&&value<='40')
    setSenti("Curious!");
    else if(value>='41'&&value<='55')
    setSenti("Just Saying!");
    else if(value>='56'&&value<='69')
    setSenti("Feeling Good!");
    else if(value>='70'&&value<='80')
    setSenti("Being Happy!");
    else if(value>='90'&&value<='100')
    setSenti('Love!');
  }
  return (
    <div className={classes.root}>
      <SentimeterSlider
        defaultValue={50}
        aria-labelledby="discrete-slider"
        valueLabelDisplay="auto"
        step={1}
        marks={[]}
        min={0}
        max={+100}
        onChange={(event,value)=>handleSlider(value)}
        onChangeCommitted={(event,value)=>props.handleChange({sentimeterValue:value,sentimeter:senti})}
      /><span>Sentiment:<b>{senti}</b></span>
    </div>
  );
}
