import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import Loader from 'react-loader-spinner';

export const LoaderTailSpin= ()=>
{
    return <Loader
    type="TailSpin"
    color="red"
    height={100}
    width={100}
    style={{position:"fixed",top:"40%",left:"43%"}}
 />
}

