import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';
import { Button } from '@material-ui/core';
import Sentimeter from './sentimeter';

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    marginLeft:30,
    marginBottom:20,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  filterButton:
  {
      marginTop:22
  }
}));

export default function SentiFilter(props) {
    const classes = useStyles();
    const [state, setState] = React.useState('');
    
      const handleChange = (event) => {
        const name = event.target.name;
        setState(event.target.value);
      };

  return (
    <div>
        {/* <Sentimeter></Sentimeter> */}
        <FormControl className={classes.formControl}>
        <InputLabel htmlFor="age-native-simple">Filter</InputLabel>
        <Select
          native
          onChange={handleChange}
        >
          <option aria-label="None" value=""></option>
          <option value='Hate It!'>Hate It!</option>
          <option value='Angry!'>Angry!</option>
          <option value='Sad :('>Sad :(</option>
          <option value='Curious!'>Curious!</option>
          <option value='Just Saying!'>Just Saying!</option>
          <option value='Feeling Good!'>Feeling Good!</option>
          <option value='Being Happy!'>Being Happy!</option>
          <option value='Love!'>Love!</option>
        </Select>
      </FormControl>
        <Button variant="contained"
        color="primary"
        size="small" 
        onClick={e=>props.handleChange(state)}
        className={classes.filterButton}>Go</Button>
    </div>
  );
}
