import React, { useEffect, useState, useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { PostCard } from './post-card';
import myFirebase from '../../firebase/firebase';
import { Button } from "@material-ui/core";
import SentiFilter from './senti-filter';
const useStyles = makeStyles((theme) => ({
  optionBar:
  {
    backgroundColor: 'white',
    borderRadius: 5
  },
  filter:
  {
  }
}));

export function Feed() {
  const classes = useStyles();
  var [feedStream, setFeedStream] = React.useState([]);
  const fetchFilterData = async (value) => {
    var db = myFirebase.firestore();
    db.collection("posts")
      .where("sentiment", "==", value)
      .orderBy('createdAt', "desc")
      .limit(30).
      get().then((querySnapshot) => {
        console.log(querySnapshot);
        setFeedStream([]);
        querySnapshot.forEach((doc) => {
          console.log(doc);
          setFeedStream(oldArray => [...oldArray, doc]);
        });
      });
    };
    const fetchData = async () => {
      var db = myFirebase.firestore();
      db.collection("posts").orderBy('createdAt', "desc")
        .limit(30).
        get().then((querySnapshot) => {
          setFeedStream([]);
          querySnapshot.forEach((doc) => {
            setFeedStream(oldArray => [...oldArray, doc]);
          });
        });
    };

    const fetchNextData = async () => {
      var db = myFirebase.firestore();
      console.log(feedStream);
      const lastVisible = feedStream && feedStream[feedStream.length - 1];
      lastVisible && db.collection("posts").orderBy('createdAt', "desc")
        .startAfter(lastVisible)
        .limit(30).
        get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            setFeedStream(oldArray => [...oldArray, doc]);
          });
        })
        .catch();
    };

  const handleFilterChange = (value) => {
    if(value != "")
        fetchFilterData(value);
    else
      fetchData();
  };
 
  useEffect(() => {
    fetchData();
  }, []);
  return (
    <div className={classes.root}>
      <div className={classes.optionBar}>
        <SentiFilter className={classes.filter} handleChange={handleFilterChange}></SentiFilter>
      </div>
      { feedStream.map(val => <PostCard data={val.data()}></PostCard>)}
      <div className="row">
        <Button color="primary" className="m-auto" onClick={fetchNextData}>Load more</Button>
      </div>
    </div>
  );
}
